.studentInfoContainer{
    width:100%;
    background-color:white;
}
.studenPhoto{

width: 30%;
display: inline-block;
margin: 0;    
float: left;
-ms-flex-item-align: center;
    align-self: center;
margin-right: 5px;
padding-right: 5px;
border-right: 1px solid grey;
}
.studentInfo{
    display: inline-block;
    margin: 0;    
    
}

.card {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    width: 40%;
    display: inline-block;
margin: 0;    
}

.studentInfoContainer:hover {
    -webkit-box-shadow: 3 8px 16px 0 rgba(0, 0, 0, 0.459);
            box-shadow: 3 8px 16px 0 rgba(0, 0, 0, 0.459);
}



.btn{

    width: 100%
}

.list-group{
    float: left
}



