select {
    width: 50%;
    padding: 16px 20px;
    border: none;
    border-radius: 4px;
    background-color: #f1f1f1;
    
}



input[type=button], input[type=submit], input[type=reset] {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 16px 32px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
}
textarea {
    width: 100%;
    height: 150px;
    padding: 12px 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
    resize: none;
}
input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}


.formStyle{
    border-radius: 5px;
    width: 50%;
    padding: 10px;
    text-align: center;
    width: 100%;
    

}

.formStyle select {
float: left;
    
}
.newMessageContainer{
    padding: 10px;
        text-align: center;
        width: 100%;
        border-top: 1px solid #FBB040;
        
    
    }

    .exampleText{

        width: 100%
    }

    .submitBtn{

width: 100%
    }


